@import url("https://fonts.googleapis.com/css2?family=REM:wght@100;200;400;500;600;700;800&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
html {
  @apply relative scroll-smooth;
}

body {
  font-size: 15px;
  @apply overflow-x-hidden text-gray-700;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @apply font-semibold;
}